import React, { useEffect, useRef, useState } from "react";
import { motion } from "framer-motion";
import styles from "./styles.module.scss";
import Button from "../../Button/Button";
import closeIcon from "../../../assets/images/icons/icons8-close-48.png";

const calculateProduction = (mineLevel, nftName) => {
  const currentProduction = calculateSingleProduction(mineLevel, nftName);
  const nextProduction = calculateSingleProduction(mineLevel + 1, nftName);
  const currentCost = calculateUpgradeCost(mineLevel, nftName);
  const nextCost = calculateUpgradeCost(mineLevel + 1, nftName);
  const currentUpgradeTime = calculateUpgradeTime(mineLevel, nftName);
  const nextUpgradeTime = calculateUpgradeTime(mineLevel + 1, nftName);

  return {
    currentProduction,
    nextProduction,
    currentCost,
    nextCost,
    currentUpgradeTime,
    nextUpgradeTime,
  };
};

const calculateSingleProduction = (mineLevel, nftName) => {
  return parseFloat(
    (
      (nftName === "Chaos X-18 Mine Aurum" ? 3 : 2) * mineLevel * Math.pow(1.12, mineLevel)
    ).toFixed(3)
  );
};
  
const calculateUpgradeCost = (mineLevel, nftName) => {
  let aurumCost = 0;
  let celiumCost = 0;
  let upgradeNFT = "Upgrade NFT";

  if (nftName === "Chaos X-18 Mine Aurum") {
    aurumCost = (56 * Math.pow(1.44, mineLevel - 1)).toFixed(3);
    celiumCost = (14 * Math.pow(1.44, mineLevel - 1)).toFixed(3);
  } else if (nftName === "ChaosX-18 Mine Celium") {
    aurumCost = (46 * Math.pow(1.58, mineLevel - 1)).toFixed(3);
    celiumCost = (23 * Math.pow(1.58, mineLevel - 1)).toFixed(3);
  }

  return {
    aurumCost,
    celiumCost,
    upgradeNFT,
  };
};
  
const calculateUpgradeTime = (mineLevel, nftName) => {
  const totalSeconds = Math.floor(11 * Math.pow(1.55, mineLevel));
  const hours = Math.floor(totalSeconds / 3600);
  const minutes = Math.floor((totalSeconds % 3600) / 60);
  const seconds = totalSeconds % 60;
  return `${hours} hours ${minutes} minutes ${seconds} seconds`;
};

/*start custom*/

const [timeRemaining, setTimeRemaining] = useState(0);

const startUpgradeTimer = () => {
  const upgradeTime = new Date(mine.lastUpgrade).toLocaleString();

  if (upgradeTime === "1.1.1970, 00:00:00") {
    return; // Upgrade is not in progress
  }

  const neededTime = Math.floor(11 * Math.pow(1.55, mine.level)) * 1000;

  if (neededTime > 0) {
    setTimeRemaining(neededTime);

    const timer = setInterval(() => {
      setTimeRemaining((prevTimeRemaining) => {
        const updatedTimeRemaining = prevTimeRemaining - 1000;

        if (updatedTimeRemaining <= 0) {
          clearInterval(timer);
          setTimeout(() => {
            window.location.reload();
          }, 1000);
          return 0;
        } else {
          return updatedTimeRemaining;
        }
      });
    }, 1000);
  }
};

useEffect(() => {
  startUpgradeTimer();
}, []);

const formatTimeRemaining = (time) => {
  const hours = Math.floor(time / 3600000);
  const minutes = Math.floor((time % 3600000) / 60000);
  const seconds = Math.floor((time % 60000) / 1000);

  return `${hours} hours ${minutes} minutes ${seconds} seconds`;
};


/*end custom*/


const ViewNftDetailsModal = ({ onClose, description, effect, currentLevel, nextLevel, nftName }) => {
  const modalRef = useRef(null);
  const modalTransition = {
    type: "spring",
    ease: "easeInOut",
    damping: 30,
  };
    
  return (
    <motion.div
      className={styles.container}
      transition={modalTransition}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      ref={modalRef}
      onClick={onClose}
    >
		<div className={styles.container_modal} onClick={(e) => e.stopPropagation()}>
        <div className={styles.container_modal_modalHeader}>
          <h3>Description</h3>
          <img
            rel="preload"
            className={styles.container_modal_modalHeader_close}
            src={closeIcon}
            onClick={onClose}
            alt="close icon"
          />
        </div>

        <div className={styles.container_modal_modalBody}>
          <span>{description}</span>
          {effect !== "No effect" && (
            <div>
              <h3>Effect</h3>
              <table>
                <thead>
                  <tr>
                    <th></th>
                    <th>Current Level</th>
                    <th>Next Level</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>Current Production</td>
                    <td>{calculateProduction(currentLevel, nftName).currentProduction}  </td>
                    <td>{calculateProduction(nextLevel, nftName).currentProduction}  </td>
                  </tr>
                  <tr>
                    <td>Current Cost (Aurum)</td>
                    <td>{calculateProduction(currentLevel, nftName).currentCost.aurumCost}  </td>
                    <td>{calculateProduction(nextLevel, nftName).currentCost.aurumCost}  </td>
                  </tr>
                  <tr>
                    <td>Current Cost (Celium)</td>
                    <td>{calculateProduction(currentLevel, nftName).currentCost.celiumCost}  </td>
                    <td>{calculateProduction(nextLevel, nftName).currentCost.celiumCost}  </td>
                  </tr>
				  <tr>
                    <td>Burner</td>
                    <td>{calculateUpgradeCost(currentLevel, nftName).upgradeNFT}</td>
                    <td>{calculateUpgradeCost(nextLevel, nftName).upgradeNFT}</td>
                  </tr>
                  <tr>
                    <td>Upgrade Time </td>
                    <td>{calculateProduction(currentLevel, nftName).currentUpgradeTime}  </td>
                    <td>{calculateProduction(nextLevel, nftName).currentUpgradeTime}  </td>
                  </tr>
				  <tr>
                    <td>Time remaining for upgrade:</td>
                    <td>
                      <b>
                        {timeRemaining > 0
                          ? formatTimeRemaining(timeRemaining)
                          : "None"}
                      </b>
                    </td>
                    <td></td>
                  </tr>				  
                </tbody>
              </table>
            </div>
          )}
        </div>

        <div className={styles.container_modal_modalFooter}>
          <Button onClick={onClose} size="medium" color="blue">
            Close
          </Button>
        </div>
      </div>
    </motion.div>
  );
};

export {
  calculateProduction,
  calculateSingleProduction,
  calculateUpgradeCost,
  calculateUpgradeTime,
};

export default ViewNftDetailsModal;
